.banners {
  padding: 50px 0;

  &__container {
    @include mobile {
      padding: 0;
    }
  }

  &__list {
    display: flex;

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__item {
    flex: 1 0 33%;

    &:not(:last-child) {
      margin-right: 0.8%;

      @include small-tablet {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }

    @include small-tablet {
      flex: 1 1 100%;
    }
  }

  &__article {}
}

.article-banner {
  padding: 50px;
  padding-right: 35%;
  min-height: 290px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #d7d7d7;

  &__title {
    margin: 0;
    margin-bottom: 20px;
    line-height: 1.2;
    font-size: 32px;

    @include desktop {
      font-size: 28px;
    }

    @include mobile {
      font-size: 26px;
    }
  }

  &__link {}

  @include tablet {
    padding-right: 70px;
    padding-left: 20px;
    background-position: calc(100% + 120px) center;
  }

  @include small-tablet {
    padding-right: 160px;
    min-height: 250px;
    background-position: center;
  }

  @include mobile {
    padding-right: 90px;
    min-height: 200px;
  }
}

.article-banner--midseason {
  background-image: url('../img/midseason.jpg');
}

.article-banner--romance {
  background-image: url('../img/romance.jpg');
}

.article-banner--acces {
  background-image: url('../img/acces.jpg');
}
