.nav {
  position: relative;

  &__list {
    display: flex;
    align-items: center;

    @include tablet {
      display: block;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 40px;

      @include tablet {
        margin-right: 0;
      }
    }
  }

  &__link {

    &:hover {
      @include tablet {
        &::before {
          opacity: 0;
        }
      }
    }

    &--current {
      &::before {
        opacity: 1;
        transition: opacity 0.15s;

        @include tablet {
          opacity: 0;
        }
      }
    }

    @include tablet {
      display: inline-block;
      padding: 15px 10%;
      border-bottom: 1px solid var(--dark-color);
      width: 100%;
      font-weight: 700;
      font-size: 20px;
    }
  }

  @include tablet {
    position: fixed;
    left: -150vw;
    top: 0;
    padding: 10% 0;
    width: 80%;
    height: 100vh;
    background-color: #ccc;
    transition: left 0.3s;
  }

  &__close {
    display: none;

    @include tablet {
      display: block;
      position: absolute;
      top: 20px;
      right: 20px;
      z-index: 50;
      width: 15px;
      height: 15px;
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &--visible {
    left: 0;
  }
}
