.card-content {
  padding-top: 80px;

  @include small-tablet {
    padding-top: 50px;
  }
}

.card-description {
  display: flex;
  align-items: flex-start;

  &__left {
    padding-top: 30px;
    height: 270px;
    width: 15%;

    @include desktop {
      width: 35%;
    }

    @include mobile {
      padding-top: 0;
      width: 100%;
    }
  }

  &__navigation {
    padding: 20px 0;
    width: 100%;
    height: 210px;
    overflow: auto;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 50px;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 15px;
    color: var(--gray-color);
    transition: color 0.3s;

    &:hover {
      color: var(--dark-color);
    }

    &--active {
      color: var(--dark-color);
      pointer-events: none;
    }
  }

  &__right {
    padding-top: 10px;
    padding-right: 120px;
    padding-left: 125px;
    width: 85%;

    @include desktop {
      padding-right: 50px;
      padding-left: 50px;
    }

    @include small-tablet {
      padding: 15px;
    }

    @include mobile {
      padding: 15px 0;
      width: 100%;
    }
  }

  &__content {
    display: none;

    p {
      margin: 0;
      margin-bottom: 30px;
      font-weight: 400;
      line-height: 1.85;
      font-size: 14px;
    }

    &--active {
      display: block;
    }
  }

  @include mobile {
    flex-wrap: wrap;
  }
}

.simplebar-track .simplebar-vertical {
  width: 3px;
  background-color: #ccc;
}

.simplebar-placeholder {
  display: none;
}

.card-related {
  padding-top: 40px;

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 24px;
  }

  &__slider {
    position: relative;
    padding-bottom: 90px;
    overflow: hidden;
  }

  @include mobile {
    padding-top: 0;
  }
}

.related-pag {
  left: 0;
  bottom: 0;
  padding-left: 20px;
  text-align: left;

  .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    opacity: 0.5;
    background-color: #ccc !important;

    &:not(:last-child) {
      margin-right: 15px !important;
    }
  }

  .swiper-pagination-bullet-active {
    position: relative;
    background-color: var(--dark-color) !important;
    opacity: 1;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: -1;
      border-radius: 100%;
      width: 20px;
      height: 20px;
      background-color: #ccc !important;
      transform: translate(-50%, -50%);
    }
  }
}
