.catalog-grid {
  padding-top: 2.2rem;
  width: 990px;

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 70px;

    @include tablet {
      justify-content: space-between;
    }

    @include mobile {
      margin-bottom: 50px;
    }
  }

  &__item {
    margin-bottom: 3rem;

    @include tablet {
      width: 31% !important;
    }

    @include mobile {
      width: 48% !important;
    }
  }
}

[data-grid-columns="3"] {
  .catalog-grid__item {
    width: 31.3%;

    &:not(:nth-child(3n)) {
      margin-right: 3%;
    }

    &:nth-last-child(-n+3) {
      margin-bottom: 0;
    }
  }
}

[data-grid-columns="4"] {
  .catalog-grid__item {
    width: 22.7%;

    &:not(:nth-child(4n)) {
      margin-right: 3%;

      @include tablet {
        margin-right: 0 !important;
      }
    }

    &:nth-last-child(-n+4) {
      margin-bottom: 0;
    }
  }
}

[data-grid-columns="5"] {
  .catalog-grid__item {
    width: 17.6%;

    &:not(:nth-child(5n)) {
      margin-right: 3%;
    }

    &:nth-last-child(-n+5) {
      margin-bottom: 0;
    }
  }
}

.catalog-columns {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;

  &__list {
    display: flex;
    align-items: center;
  }

  &__text {
    margin-right: 0.5rem;
  }

  &__item:not(:last-child) {
    margin-right: 0.5rem;
  }

  &__btn {
    padding: 0 5px;
    font-weight: 700;
    font-size: 11px;
    color: var(--tundora-gray);

    &:hover {
      color: var(--dark-color);
    }

    &--current {
      color: var(--dark-color);

      &::before {
        opacity: 1;
      }
    }
  }

  @include desktop {
    display: none;
  }
}

.catalog-props {
  margin-bottom: 1.25rem;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.56rem;
  }
}

.catalog-choice {
  display: none;
  font-size: 0;

  &__item {
    padding: 5px 12px;
    border-radius: 0.75rem;
    font-size: 0.9rem;
    color: var(--tundora-gray);
    background-color: #eee;
    transition: background-color 0.3s ease, color 0.3s ease;

    svg {
      margin-left: 5px;
      width: 8px;
      height: 9px;
      fill: currentColor;
      pointer-events: none;
    }

    &:hover {
      color: var(--light-color);
      background-color: var(--dark-color);
    }
  }

  &__item:not(:last-child) {
    margin-right: 10px;
    margin-bottom: 5px;
  }

  &__clear {
    position: relative;
    font-weight: 400;
    font-size: 14px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: -4px;
      width: 100%;
      height: 1px;
      background-color: currentColor;
      opacity: 1;
      transition: opacity 0.15s;
    }
  }
}
