.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: var(--quill-gray);

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(21, 21, 21, 0.05);
  }

  &__container {
    position: relative;
    height: 100%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 220px;
    height: 100%;

    @include desktop {
      padding: 0 50px;
    }

    @include tablet {
      padding: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 45px;
    width: 555px;
    font-weight: 700;
    line-height: 1.2;
    font-size: 72px;

    @include tablet {
      font-size: 60px;
    }

    @include small-tablet {
      max-width: 400px;
      font-size: 45px;
    }

    @include mobile {
      max-width: 200px;
      font-size: 38px;
    }
  }

  &__descr {
    margin: 0;
    font-size: 18px;
    line-height: 1.2;

    @include tablet {
      font-size: 16px;
    }

    @include small-tablet {
      max-width: 300px;
    }
  }

  &__image {
    position: absolute;
    right: 130px;
    bottom: 0;
    max-height: 80%;

    @include desktop {
      right: 15px;
    }

    @include tablet {
      right: 0;
      max-height: 70%;
    }

    @include small-tablet {
      max-height: 60%;
    }

    @include mobile {
      display: none;
    }
  }

  .swiper-pagination {
    @include mobile {
      // display: none;
    }
  }

  @include tablet {
    min-height: 580px;
  }
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;

  .swiper-slide {
    height: 100%;
  }
}

.swiper-horizontal>.swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction,
.banner-pag {
  left: 50%;
  bottom: 40px;
  transform: translateX(-50%);

  .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    opacity: 0.5;
    background-color: var(--light-color);

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  .swiper-pagination-bullet-active {
    position: relative;
    background-color: var(--dark-color);
    opacity: 1;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: -1;
      border-radius: 100%;
      width: 20px;
      height: 20px;
      background-color: var(--light-color);
      transform: translate(-50%, -50%);
    }
  }
}

.marketing {
  position: absolute;
  left: 20px;
  bottom: 30px;
  z-index: 15;
  display: flex;
  padding: 10px;
  width: 280px;
  background-color: var(--light-color);
  box-shadow: 0px 5px 30px 0px rgba(0, 0, 0, 0.1);
  transform: translateX(-150vw);
  transition: transform 0.3s;

  &--visible {
    transform: translateX(0);
    transition: transform 0.3s;
  }

  &__image {
    flex-shrink: 0;
    margin-right: 10px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__content>:not(:last-child) {
    margin-bottom: 10px;
  }

  &__text {
    margin: 0;
    line-height: 1.2;
    font-size: 11px;
    color: var(--gray-color);
  }

  &__title {
    margin: 0;
    max-width: 155px;
    min-height: 36px;
    line-height: 1.2;
    font-size: 15px;

    @include small-tablet {
      min-height: 15px;
      font-size: 14px;
    }
  }

  &__when-from {
    margin: 0;
    max-width: 145px;
    font-size: 11px;
    line-height: 1.2;
    font-style: italic;
    color: var(--gray-color);

    @include small-tablet {
      max-width: 115px;
    }
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 0;
    width: 10px;
    height: 10px;
    background-image: url('../img/svg/close.svg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  @include small-tablet {
    height: 100px;
    width: 220px;
  }
}
