.catalog-content {
  position: relative;
  min-height: 500px;

  &__container {
    display: flex;
  }
}

.catalog-filters {
  margin-right: 30px;
  padding-top: 30px;
  padding-right: 60px;
  width: 310px;

  @include tablet {
    padding-right: 30px;
    width: 50%;
  }

  @include small-tablet {
    display: none;
    position: absolute;
    top: 82px;
    left: 0;
    z-index: 50;
    padding: 30px;
    padding-right: 150px;
    width: 100%;
    background-color: #fff;
  }

  @include mobile {
    padding-right: 30px;
  }
}

.catalog-filters--open {
  display: block;
}

.catalog-filter {
  padding-bottom: 30px;
  border-top: 1px solid #eee;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 2rem;
    margin-bottom: 1.25rem;
    cursor: pointer;
    user-select: none;
  }

  &__caption {
    display: flex;
    align-items: center;
  }

  &__title {
    display: inline-block;
    margin: 0;
    font-weight: 700;
    text-transform: uppercase;
  }

  &__quantity {
    margin-left: 5px;
    min-width: 16px;
  }

  &__toggle {
    position: relative;
    width: 12px;
    height: 12px;
    color: #aaa;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      width: 2px;
      height: 100%;
      background-color: currentColor;
      transform: translateX(-50%);
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      transform: translateY(-50%);
    }
  }

  &__bottom {
    display: none;
  }

  &__items {
    columns: 2;
  }

  &__item {
    margin-bottom: 10px;

    &:nth-child(5n) {
      margin-bottom: 0;
    }
  }

  &:last-child {
    border-bottom: 1px solid #eee;
  }

  &--open {
    .catalog-filter__bottom {
      display: block;
    }

    .catalog-filter__toggle {
      &::before {
        display: none;
      }
    }
  }
}

.catalog-mobile-filters {
  display: none;

  @include small-tablet {
    display: inline-flex;
    align-items: center;
    font-weight: 700;
    font-size: 16px;

    svg {
      margin-right: 10px;
      width: 20px;
      height: 20px;
    }
  }
}

.hide-filters {
  margin-bottom: 20px;
  padding-left: 24px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  background-image: url("../img/cross.png");
  background-position: left center;
  background-repeat: no-repeat;
}

.custom-checkbox {
  display: inline-block;
  // display: flex;
  // align-items: center;
  // justify-content: flex-start;
  cursor: pointer;
  user-select: none;

  &__text {
    position: relative;
    display: inline-block;
    padding-left: 18px;
    font-size: 15px;
    color: var(--gray-color);
    transition: color 0.3s ease-in-out;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 3px;
      border: 2px solid #aaa;
      width: 12px;
      height: 12px;
      transition: border 0.3s ease-in-out;
    }

    &::after {
      content: "";
      position: absolute;
      left: 3px;
      top: 3px;
      width: 6px;
      height: 10px;
      background-image: url("../img/check.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &:hover {
      color: var(--dark-color);
      transition: color 0.3s ease-in-out;
    }
  }

  &__input {
    &:checked+span {
      color: var(--dark-color);

      &::before {
        border-color: var(--dark-color);
      }

      &::after {
        opacity: 1;
      }
    }
  }
}

.custom-select {
  position: relative;
  min-width: 115px;
  user-select: none;

  &__dropdown {
    position: absolute;
    left: 0;
    top: calc(100% + 2px);
    z-index: 50;
    display: none;
    padding: 8px;
    width: 100%;
    background-color: var(--light-color);
  }

  &__item {
    cursor: pointer;
    transition: color 0.2s;

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    &:hover {
      color: var(--gray-color);
    }
  }

  &__top {
    position: relative;
    padding-right: 30px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e5e5e7;
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
    cursor: pointer;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -5px;
      width: 10px;
      height: 6px;
      transform: translateY(-50%);
      background-image: url('../img/angle-down.png');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &--open {
    .custom-select__top {
      &::after {
        transform: translateY(-50%) rotate(180deg);
      }
    }

    .custom-select__dropdown {
      display: block;
    }
  }

  &--footer {
    min-width: auto;

    .custom-select__top {
      padding-right: 15px;
      padding-bottom: 0;
      border-bottom: none;
      font-size: 13px;

      &::after {
        margin-top: 0;
      }
    }
  }
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;

  &__item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__link {
    display: inline-flex;
    align-items: center;
    padding: 0 15px;
    border: 2px solid #eee;
    height: 40px;
    font-weight: 700;
    font-size: 11px;
    color: var(--gray-color);
    text-transform: uppercase;
    transition: color 0.2s, background-color 0.2s, border 0.2s;

    svg {
      margin-left: 5px;
      width: 8px;
      height: 16px;
      fill: currentColor;
    }

    &:hover {
      border: 2px solid var(--dark-color);
      color: var(--light-color);
      background-color: var(--dark-color);
      transition: color 0.2s, background-color 0.2s, border 0.2s;
    }

    &--current {
      border: 2px solid var(--dark-color);
      color: var(--light-color);
      background-color: var(--dark-color);
    }

    @include mobile {
      height: 35px;
    }
  }
}
