.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  padding: 40px 0;
  width: 100%;
  background-color: rgb(234, 234, 234);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__shop-nav {
    margin-left: 250px;

    @include tablet {
      margin-left: 0;
    }
  }
}

.burger {
  display: none;
  @include burger;

  @include tablet {
    display: block;
  }
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;

  @include mobile {
    width: 100px;
  }
}

.catalog-header {
  background-color: var(--light-color);
}

.header-padding {
  padding-top: 50px;

  @include mobile {
    padding-top: 40px;
  }
}

.free-delivery {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 200;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 30px;
  font-size: 12px;
  text-transform: uppercase;
  color: var(--light-color);
  background-color: #111;

  &__btn {
    position: absolute;
    top: 50%;
    right: 40px;
    width: 12px;
    height: 12px;
    transform: translateY(-50%);

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--light-color);
    }
  }

  @include mobile {
    display: none;
  }
}
