.shop-banners {
  position: relative;
  padding: 50px 0;

  &__container {
    display: flex;
    justify-content: space-between;
    padding: 0;

    @include mobile {
      display: block;
    }
  }

  &__image {
    &:not(:last-child) {
      @include mobile {
        margin-bottom: 20px;
      }
    }
  }

  @include small-tablet {
    padding: 0;
  }
}

.shop-banner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
  width: 50%;
  min-height: 420px;
  color: var(--light-color);
  background-color: var(--dark-color);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background-color: rgba(#d70808, 0.1);
  }

  &__title {
    position: relative;
    z-index: 5;
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 40px;

    @include small-tablet {
      margin-bottom: 15px;
      font-size: 32px;
    }
  }

  &__link {
    display: block;
  }

  &__link::before {
    position: absolute;
    content: "";
    inset: 0;
    width: 100%;
    height: 100%;
  }

  &__text {
    position: relative;
    z-index: 5;
    font-size: 18px;
  }

  @include tablet {
    padding: 0;
    min-height: 300px;
  }

  @include mobile {
    min-height: 200px;
    width: 100%;
  }
}

.shop-banner--men {
  background-image: url('../img/banner-1.jpg');
}

.shop-banner--women {
  background-image: url('../img/banner-2.jpg');
}
