.breadcrumbs {
  &__list {
    display: flex;
    align-items: center;
    padding-top: 35px;
    padding-bottom: 25px;
    border-bottom: 1px solid #eee;
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 35px;

      &::after {
        content: '/';
        position: absolute;
        right: -20px;
        top: 50%;
        font-size: 10px;
        text-transform: uppercase;
        color: var(--gray-color);
        transform: translateY(-50%);
      }
    }
  }

  &__link {
    font-size: 10px;
    text-transform: uppercase;
    color: var(--gray-color);
    transition: color 0.3s;

    &:hover {
      color: var(--dark-color);
    }

    &--curent {
      color: var(--dark-color);
    }
  }
}

.breadcrumbs .card-prev,
.breadcrumbs .card-next {
  display: none;
}

.card {

  .breadcrumbs .card-prev,
  .breadcrumbs .card-next {
    display: inline-block;
    font-weight: 700;
    font-size: 10px;
    color: var(--gray-color);
    text-transform: uppercase;
    transition: color 0.3s;

    &:hover {
      color: var(--dark-color);
    }
  }
}
