.card {
  padding-top: 150px;

  .breadcrumbs {
    display: flex;
    align-items: center;

    &__list {
      padding-top: 0;
      padding-bottom: 0;
      border-bottom: none;

      @include mobile {
        margin-bottom: 15px;
        width: 100%;
      }
    }

    @include mobile {
      flex-wrap: wrap;
    }
  }

  .card-prev {
    margin-left: auto;

    @include mobile {
      margin-left: 0;
    }
  }

  .card-next {
    margin-left: 20px;
  }

  @include mobile {
    padding-top: 100px;
  }
}

.card-top {
  display: flex;
  padding-top: 20px;

  &__left {
    width: 57.5%;

    @include small-tablet {
      width: 100%;
    }
  }

  &__right {
    padding-left: 8%;
    padding-top: 55px;
    width: calc(100% - 57.5%);

    @include desktop {
      padding-top: 40px;
      padding-left: 5%;
    }

    @include small-tablet {
      padding-left: 0;
      width: 100%;
    }
  }

  @include small-tablet {
    flex-wrap: wrap;
  }
}

.card-slider {
  display: flex;

  &__thumbs {
    @include mobile {
      display: flex;
      margin-top: 20px;
      padding-bottom: 10px;
      overflow-x: auto;
    }
  }

  &__thumb {
    cursor: pointer;

    img {
      width: 100%;
      pointer-events: none;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    @include mobile {
      flex-shrink: 0;

      &:not(:last-child) {
        margin-right: 10px;
        margin-bottom: 0;
      }
    }
  }

  &__main {
    margin-left: 15px;
    max-width: 686px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include mobile {
      margin-left: 0;
      width: 100%;
    }
  }

  @include mobile {
    flex-direction: column-reverse;
  }
}

.card-info {
  &__prop {
    margin-bottom: 15px;
    padding-left: 20px;
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 24px;

    @include mobile {
      font-size: 22px;
    }
  }

  &__rate {
    margin-bottom: 25px;
    font-size: 13px;
    color: var(--gray-color);

    img {
      margin-right: 3px;
    }
  }

  &__price {
    margin-bottom: 20px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 50px;
    line-height: 1.40;
    color: var(--tundora-gray);

    @include desktop {
      margin-bottom: 30px;
    }
  }

  &__first-select {
    margin-bottom: 30px;
  }

  &__second-select {
    margin-bottom: 80px;

    @include desktop {
      margin-bottom: 40px;
    }
  }

  &__stepper {
    margin-bottom: 10px;
  }

  &__btn {
    margin-bottom: 10px;
    width: 100%;
    height: 58px;
    font-weight: 700;
    font-size: 12px;
    color: var(--dark-color);
    text-transform: uppercase;
    background-color: #f2f2f2;
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

    &:hover {
      color: var(--light-color);
      background-color: var(--dark-color);
    }

    &--tocart {
      color: var(--light-color);
      background-color: var(--dark-color);

      &:hover {
        color: var(--dark-color);
        background-color: #f2f2f2;
      }
    }


  }
}

.info-price {
  display: flex;
  align-items: flex-end;

  &__current {
    margin-right: 20px;
    font-weight: 700;
    font-size: 24px;
    color: var(--dark-color);
  }

  &__old {
    font-size: 16px;
    color: #959595;
    text-decoration: line-through;
  }
}

.color-select {
  &__selected {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 10px;
    color: var(--gray-color);
    text-transform: uppercase;

    span {
      color: var(--dark-color);
    }
  }

  &__list {
    display: flex;
    align-items: center;
    padding-left: 5px;

    @include desktop {
      flex-wrap: wrap;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 15px;
    }

    @include desktop {
      margin-bottom: 15px;
    }
  }

  &__btn {
    position: relative;
    border-radius: 100%;
    width: 24px;
    height: 24px;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      border-radius: 100%;
      border: 1px solid #ccc;
      width: 32px;
      height: 32px;
      transform: translate(-50%, -50%);
      transition: border 0.3s;
    }

    &:hover {
      &::before {
        border: 1px solid var(--dark-color);
      }
    }

    &--active {
      &::before {
        border: 1px solid var(--dark-color);
      }
    }
  }
}

.size-select {
  &__selected {
    margin: 0;
    margin-bottom: 13px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--gray-color);

    span {
      color: var(--dark-color);
    }
  }

  &__list {
    display: flex;
    align-items: center;
    margin-bottom: 8px;

    @include desktop {
      flex-wrap: wrap;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 5px;
    }

    @include desktop {
      margin-bottom: 10px;
    }
  }

  &__btn {
    position: relative;
    width: 42px;
    height: 32px;
    font-size: 13px;
    color: var(--gray-color);
    transition: color 0.3s;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border: 1px solid #ccc;
      width: 100%;
      height: 100%;
      transition: border 0.3s;
    }

    &:hover {
      color: var(--dark-color);

      &::before {
        border: 1px solid var(--dark-color);
        transition: border 0.3s;
      }
    }

    &--active {
      color: var(--dark-color);
      transition: color 0.3s;

      &::before {
        border: 1px solid var(--dark-color);
        transition: border 0.3s;
      }
    }
  }

  &__clear {
    display: none;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: #d24242;

    svg {
      margin-right: 2px;
      width: 8px;
      height: 8px;
      fill: currentColor;
    }
  }
}

.stepper {
  display: flex;
  border: 1px solid #ccc;
  height: 58px;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 44px;

    svg {
      display: block;
      width: 12px;
      height: 12px;
      fill: var(--gray-color);
      transition: fill 0.3s;
    }

    &:hover {
      svg {
        fill: var(--dark-color);
      }
    }

    &--disabled {
      opacity: 0.4;
      pointer-events: none;
    }
  }

  &__input {
    flex-grow: 1;
    border: none;
    height: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 13px;
    color: var(--dark-color);
    text-transform: uppercase;
  }
}

.card-bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 50px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;

  span {
    color: var(--gray-color);
  }

  &__item {
    &:not(:last-child) {
      margin-right: 10px;
    }

    @include desktop {
      margin-bottom: 10px;
    }
  }

  &__social {
    display: flex;
    align-items: center;
    margin-top: 15px;

    span {
      margin-right: 15px;
    }

    @include desktop {
      width: 100%;
    }
  }
}
