.main-products {
  padding: 50px 0;

  &__container {}

  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 35px;

      @include mobile {
        margin-right: 15px;
      }
    }
  }

  &__btn {
    line-height: 1.2;
    font-size: 24px;
    color: var(--gray-color);
    transition: color 0.15s;


    &--current {
      &::before {
        opacity: 1;
        transition: opacity 0.15s;
      }
    }

    &:hover {
      color: var(--dark-color);
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__grid {
    margin-bottom: 50px;
  }

  &__btn-center {
    text-align: center;
  }

  &__more {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    font-size: 16px;
    color: var(--gray-color);
    transition: color 0.3s;

    span {
      display: inline-block;
      margin-bottom: 5px;
    }

    svg {
      width: 12px;
      height: 21px;
      fill: var(--dark-color);
      transition: transform 0.3s;
    }

    &:hover {
      color: var(--dark-color);

      svg {
        transform: translateY(5px);
      }
    }
  }

  @include mobile {
    padding-top: 20px;
  }
}

.grid-products {
  display: grid;
  // grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  grid-template-columns: repeat(5, 1fr);
  column-gap: 1.8%;
  row-gap: 40px;

  &__item {
    border-radius: 4px;
    overflow: hidden;
  }

  @include tablet {
    grid-template-columns: repeat(3, 1fr);

  }

  @include mobile {
    grid-template-columns: repeat(2, 1fr);
  }
}
