.footer {
  &__content {
    padding: 0 220px;

    @include desktop {
      padding: 0 100px;
    }

    @include tablet {
      padding: 0 20px;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    padding: 50px 0;

    @include small-tablet {
      flex-wrap: wrap;
    }

    @include mobile {
      padding: 30px 0;
    }
  }

  &__column {
    &:nth-child(2) {
      margin-left: -100px;

      @include tablet {
        margin-left: 0;
      }
    }

    &:last-child {
      @include tablet {
        width: 40%;
      }

      @include small-tablet {
        margin-top: 40px;
        width: 80%;
      }
    }

    &--flex {
      display: flex;
      align-items: center;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 35px;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;

    @include small-tablet {
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__list {
    &--row {
      display: flex;
      align-items: center;

      .footer__item:not(:last-child) {
        margin-bottom: 0;
        margin-right: 35px;

        @include small-tablet {
          margin-right: 20px;
        }
      }
    }

    @include mobile {
      justify-content: center;
    }
  }

  &__item:not(:last-child) {
    margin-bottom: 15px;

    @include small-tablet {
      margin-bottom: 10px;
    }
  }

  &__link {
    position: relative;
    color: var(--tundora-gray);

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: currentColor;
      opacity: 0;
      transition: opacity 0.3s;
    }

    &:hover {
      &::after {
        opacity: 1;
      }
    }

    &--row {
      font-weight: 700;
      color: var(--dark-color);
    }

    @include mobile {
      font-size: 12px;
    }
  }

  &__select {
    display: flex;
    align-items: center;
    margin-left: 60px;

    &-caption {
      margin-right: 15px;
      font-size: 13px;
      color: var(--gray-color);
    }
  }

  &__copy {
    font-size: 13px;
  }

  &__form {
    margin-bottom: 25px;
    width: 400px;

    @include tablet {
      width: 100%;
    }
  }

  &__text {
    font-size: 13px;
    color: var(--tundora-gray);
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px 0;
    border-top: 1px solid #eee;

    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
      padding: 20px 0;
    }
  }

  &__left {
    @include mobile {
      margin-top: 10px;
      width: 100%;
    }
  }

  &__right {
    @include mobile {
      margin-top: 30px;
    }
  }

  &__social {
    margin-right: 20px;
  }
}

.footer--2 {
  padding-top: 80px;

  .footer__content {
    padding: 0;
    padding-bottom: 20px;
  }

  .footer__top {
    border-top: 1px solid #eee;

    @include desktop {
      flex-wrap: wrap;
    }

    @include mobile {
      padding-bottom: 0;
    }
  }

  .footer__column {
    @include mobile {
      display: flex;
      justify-content: center;
      margin-top: 30px !important;
      width: 100%;
    }
  }

  .footer__social {
    @include mobile {
      margin-right: 0;
    }
  }

  .footer__column:nth-child(2) {
    margin-left: -200px;

    @include mobile {
      margin-left: 0;
      width: 100%;
    }
  }

  .footer__column:last-child {
    @include desktop {
      flex: 1 1 100%;
      margin-top: 50px;
    }
  }

  .footer__select:nth-child(1) {
    @include desktop {
      margin-left: 0;
    }
  }

  .footer__select:nth-child(2) {
    @include mobile {
      margin-left: 30px;
    }
  }

  .footer__bottom {
    margin: 0 150px;
    padding-bottom: 0;
    border-top: none;

    @include small-tablet {
      margin: 0 50px;
    }

    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
      padding: 20px 0;
    }
  }

  .footer__left {
    @include mobile {
      display: flex;
      justify-content: center;
      order: 2;
      margin-top: 30px;
      width: 100%;
    }
  }

  .footer__right {
    @include mobile {
      margin-top: 20px;
    }
  }

  @include mobile {
    padding-top: 50px;
  }
}

.subscribe-form {
  position: relative;

  &__title {
    margin: 0;
    margin-bottom: 45px;
    font-weight: 400;
    font-size: 13px;
    color: var(--tundora-gray);

    @include small-tablet {
      margin-bottom: 25px;
    }
  }

  &__field {
    display: block;
    border-bottom: 1px solid var(--dark-color);
    padding-bottom: 10px;
    width: 100%;

    input {
      padding: 10px;
      padding-right: 100px;
      width: 100%;
      color: var(--dark-color);

      &::placeholder {
        color: rgba(#454545, 0.5);
      }
    }
  }

  &__btn {
    position: absolute;
    right: 0;
    bottom: 10px;
    z-index: 3;
    padding: 10px;
    border-radius: 3px;
    color: var(--dark-color);
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.2);
    }
  }
}

.social {
  display: flex;
  align-items: center;

  &__item:not(:last-child) {
    margin-right: 25px;
  }

  &__link {
    display: block;
    width: 16px;
    height: 16px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }

    &--tw {
      background-image: url('../img/tw.png');
    }

    &--fb {
      background-image: url('../img/fb.png');
    }

    &--in {
      background-image: url('../img/in.png');
    }

    &--pin {
      background-image: url('../img/pin.png');
    }
  }
}
