.hero-catalog {
  padding-top: 130px;

  &__container {
    position: relative;
  }

  &__slider {
    position: relative;
    overflow: hidden;
    width: 100%;
    background-color: #f8f8f8;
  }
}

.catalog-slide {
  padding: 110px;

  &__title {
    margin: 0;
    margin-bottom: 20px;
    max-width: 385px;
    font-weight: 700;
    line-height: 142%;
    font-size: 36px;

    @include mobile {
      font-size: 28px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    max-width: 410px;
    font-size: 16px;
    line-height: 175%;
    color: var(--tundora-gray);

    @include mobile {
      font-size: 14px;
    }
  }

  &__btns {
    display: flex;
    align-items: center;

    @include mobile {
      justify-content: center;
      margin-bottom: 80px;
    }
  }

  &__image {
    position: absolute;
    right: 0;
    bottom: 0;
    right: 18%;

    @include tablet {
      right: 85px;
      height: 325px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__link {
    display: inline-block;
    padding: 15px 42px;

    &:not(:last-child) {
      margin-right: 15px;
    }

    &--dark {
      border: 1px solid var(--dark-color);
      color: var(--light-color);
      background-color: var(--dark-color);
      transition: all 0.3s;

      &:hover {
        border: 1px solid var(--dark-color);
        color: var(--color-dark);
        background-color: transparent;
      }
    }

    &--light {
      border: 1px solid var(--dark-color);
      color: var(--dark-color);
      background-color: transparent;
      transition: all 0.3s;

      &:hover {
        border: 1px solid var(--dark-color);
        color: var(--light-color);
        background-color: var(--dark-color);
      }
    }

    @include mobile {
      padding: 15px 28px;
    }
  }

  @include small-tablet {
    padding: 90px 100px;
  }

  @include mobile {
    padding: 15px;
  }
}

.hero-prev-btn,
.hero-next-btn {
  position: absolute;
  left: 30px;
  top: 50%;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--dark-color);
  width: 48px;
  height: 48px;
  color: var(--dark-color);
  transform: translateY(-50%);
  transition: background-color 0.3s;

  svg {
    width: 40px;
    height: 20px;
    fill: currentColor;
    transition: fill 0.3s;
  }

  &:hover {
    background-color: var(--dark-color);
    transition: background-color 0.3s;

    svg {
      fill: var(--light-color);
      transition: fill 0.3s;
    }
  }

  @include small-tablet {
    left: 10px;
  }

  @include mobile {
    display: none;
  }
}

.hero-next-btn {
  left: auto;
  right: 30px;

  @include small-tablet {
    left: auto;
    right: 10px;
  }
}
