.product {
  position: relative;

  &__image {
    position: relative;
    margin-bottom: 30px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      transition: transform 0.3s;
    }
  }

  &__sale {
    position: absolute;
    left: 20px;
    top: 20px;
    z-index: 20;
    padding: 3px 8px;
    border: 2px solid var(--dark-color);
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--dark-color);

    @include mobile {
      top: 15px;
      border: 1px solid var(--dark-color);
      padding: 3px 5px;
    }
  }

  &__prop {
    margin-bottom: 10px;
    padding-left: 18px;
  }

  &__title {
    margin: 0;
    margin-top: auto;
    margin-bottom: 15px;
    line-height: 1.2;
    font-size: 13px;
    color: var(--tundora-color);

    a {
      &::before {
        content: '';
        position: absolute;
        inset: 0;
      }
    }
  }

  &__price {
    display: inline-block;
    font-weight: 700;
    line-height: 1.2;
  }

  &__oldprice {
    display: inline-block;
    margin-left: 8px;
    font-weight: 700;
    line-height: 1.2;
    color: var(--gray-color);
    text-decoration: line-through;
  }

  &:hover {
    .product__image {
      img {
        transform: scale(1.1);
      }
    }

    .product-custom__price {
      transform: translateY(-100%);
    }

    .product-custom__cart {
      transform: translateY(-100%);
    }
  }
}

.product-prop {
  display: inline-block;
  font-weight: 700;
  font-size: 10px;
  color: var(--dark-color);
  text-transform: uppercase;
  background-position: left center;
  background-repeat: no-repeat;
}

.product-custom {
  height: 18px;
  overflow: hidden;

  &__price {
    transition: transform 0.3s;
  }

  &__cart {
    transition: transform 0.3s;
  }

  .to-cart {
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
  }
}

.new {
  background-image: url('../img/new.png');
}

.best {
  background-image: url('../img/best.png');
}
